import fetch from '../common/js/fetch.js';


let jobServer = process.env.JOB_URL;
let collectionService = process.env.COLLECTION_URL;

/***********************************************/
export const signInAPI= data=>{return fetch(jobServer+'/api/login/in', data, 'POST')}; //
export const personSignUpAPI= data=>{return fetch(jobServer+'/api/login/register/person', data, 'POST')}; //true 
export const companySignUpAPI= data=>{return fetch(jobServer+'/api/login/register/company', data, 'POST')}; //true 
export const signOutAPI= data=>{return fetch(jobServer+'/api/login/out', data, 'POST')}; //
export const getCodeAPI= data=>{return fetch(jobServer+'/api/login/code/send', data, 'POST')}; //
export const checkEmailAPI= data=>{return fetch(jobServer+'/api/login/isExist', data, 'POST')}; //true  
export const checkCodeAPI= data=>{return fetch(jobServer+'/api/login/code/check/', data, 'POST')}; //true 

/*******************************************************/
export const userPageFindAPI= data=>{return fetch(jobServer+'/tApi/user/pageFind', data, 'POST',"")}; //true 
export const deleteUserAPI= data=>{return fetch(jobServer+'/tApi/user', data, 'DELETE',"")}; //true 
export const getUserAPI= data=>{return fetch(jobServer+'/api/user', data, 'GET',"")}; //true 
export const getGroupUserAPI= data=>{return fetch(jobServer+'/api/user/group', data, 'POST',"")}; //true 
/***********************************************/
export const dropListOneGetApi= data=>{return fetch(jobServer+'/api/drop/one', data, 'GET',"")}; //


/*******************************************************/
export const getJobTypeAPI = data=>{return fetch(jobServer+'/api/universal/list/jobType', data, 'POST')};
export const addOrEditCompanyKanBan = data=>{return fetch(jobServer+'/api/companyKanBan/', data, 'POST')};


/*******************************************************/
export const addJobAPI = data=>{return fetch(jobServer+'/tApi/jobInfo/', data, 'POST')};
export const updateJobAPI = data=>{return fetch(jobServer+'/tApi/jobInfo/', data, 'PATCH')};
export const getJobAPI = data=>{return fetch(jobServer+'/api/jobInfo', data, 'GET')};
export const deleteJobAPI = data=>{return fetch(jobServer+'/tApi/jobInfo', data, 'DELETE')};
export const companyJobPageAPI = data=>{return fetch(jobServer+'/tApi/jobInfo/company/pageFind', data, 'POST')};
export const personJobPageAPI = data=>{return fetch(jobServer+'/api/jobInfo/normal/pageFind', data, 'POST')};
/*******************************************************/
export const addCompanyAPI = data=>{return fetch(jobServer+'/api/companyInfo/', data, 'POST')};
export const updateCompanyAPI = data=>{return fetch(jobServer+'/api/companyInfo/', data, 'PATCH')};
export const getCompanyAPI = data=>{return fetch(jobServer+'/api/companyInfo', data, 'GET')};//
export const getCompanyListAPI = data=>{return fetch(jobServer+'/api/companyInfo/all', data, 'GET')}; //

/*******************************************************/
export const getRegionAPI = data=>{return fetch(jobServer+'/api/region/', data, 'POST')};//

/*******************************************************/
export const getToFromMessage = data=>{return fetch(collectionService +'/tApi/message', data, 'GET')};//
export const getRecentUserList = data=>{return fetch(collectionService +'/tApi/message/userList', data, 'POST')};//



